@import './../../../styles/utils/mixins';
@import './../../../styles/utils/vars';
@import './../../../styles/headingStyles';

.cartPage {
  padding-top: calc(24px + $small-nav-height);

  @include on-desktop {
    padding-top: calc(40px + $big-nav-height);
  }

  &__productsBlock {
    margin-block: 32px;
    display: grid;
    gap: 16px;
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 160px;
  gap: 16px;

  border: 1px solid $shadow-color;
  border-radius: 16px;
  box-sizing: border-box;
  padding: 16px;

  @include on-tablet {
    flex-direction: row;
  }

  &__top {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;

    &--leftBlock {
      display: flex;
      align-items: center;
      gap: 16px;

      @include on-tablet {
        gap: 24px;
      }
    }

    @include on-tablet {
      justify-content: flex-start;
      gap: 24px;
    }
  }

  &__bottom {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include on-tablet {
      justify-content: flex-end;
      gap: 53px;
    }
  }

  &__numBlock {
    display: flex;
    align-items: center;
    gap: 14px;

    @include on-tablet {
      position: absolute;
      right: 128px;
    }

    &--button {
      width: 32px;
      height: 32px;
      border: 1px solid $arrow-border-color;
      background-color: $white-bg-color;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }
  }

  &__button {
    padding: 0;
    width: 16px;
    height: 16px;
    border: none;
    background-color: $white-bg-color;
    cursor: pointer;
  }

  &__image {
    height: 80px;
    width: 80px;
    object-fit: contain;
  }
}
