.icon {
  background-repeat: no-repeat;
  background-position: center;

  &__logo {
    height: 22px;
    width: 64px;
    margin: 13px 16px;
    background-size: contain;
    background-image: url(./../../images/Header/Logo.svg);
  }

  &__menu {
    width: 16px;
    height: 16px;
    padding: 16px;
    background-image: url(./../../images/Header/Menu.svg);
    box-shadow: -1px 0 0 0 $shadow-color;
  }

  &__counter {
    display: none;
  }

  @include on-tablet {
    &__menu {
      display: none;
    }

    &__counter {
      width: 14px;
      height: 14px;
      border-radius: 50%;
      border: 1px solid $white-bg-color;
      background-color: $button-item-color;
      box-sizing: border-box;

      position: absolute;
      top: 10px;
      right: 58px;

      display: flex;
      align-items: center;
      justify-content: center;

      &--cart {
        right: 10px;
      }

      &--text {
        color: $white-bg-color;
        font-family: MontRegular;
        font-size: 9px;
      }
    }

    &__favourites,
    &__cart {
      height: 16px;
      width: 16px;
    }

    &__Imglink {
      padding: 16px;
      box-shadow: -1px 0 0 0 $shadow-color;
    }

    &__activeImgLink {
      color: $nav-activelink-underline;
      border-bottom: 3px solid $nav-activelink-underline;
      padding-bottom: 13.6px;
    }

    &__favourites {
      background-image: url(./../../images/Header/Favourites.svg);
    }

    &__cart {
      background-image: url(./../../images/Header/ShoppingBag.svg);
    }
  }

  @include on-desktop {

    &__counter {
      top: 18px;
      right: 82px;

      &--cart {
        right: 18px;
      }
    }

    &__Imglink {
      padding: 24px;
      box-shadow: -1px 0 0 0 $shadow-color;
    }

    &__activeImgLink {
      color: $nav-activelink-underline;
      border-bottom: 3px solid $nav-activelink-underline;
      padding-bottom: 21.6px;
    }

    &__logo {
      width: 80px;
      height: 28px;
      margin: 18px 24px;
    }
  }
}
